.create-post {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-top: 10vh;
  width: 100vw;
  height: calc(100vh - 100px);
  margin-top: 100px;
  flex-direction: column;
}
.create-post h2 {
  margin-bottom: 50px;
}

.create-post-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 400px;
}
.create-post-form input, .create-post-form textarea {
  font-size: 16px;
  border-radius: 5px;
  margin: 2px;
  border: none;
  background-color: var(--white);
  padding: 10px;
  border: 1px solid gray;
}
.create-post-form .submit-button {
  margin: auto;
  font-size: 16px;
  padding: 10px 20px;
  background: none;
  transition: 0.2s ease-in-out;
  color: rgb(100, 100, 100);
  border: 1px solid rgb(100, 100, 100);
  border-radius: 10px;
  font-weight: bold;
  margin-top: 20px;
}
.create-post-form .submit-button:hover {
  background-color: var(--green);
  cursor: pointer;
  color: var(--white);
}
.create-post-form .text-input {
  max-height: 400px;
  height: 20vh;
  resize: none;
  font-family: inherit;
}
.create-post-form .title-input {
  width: 150px;
}

@media screen and (max-width: 600px) {
  .create-post-form {
    width: 70vw;
    margin: 0 auto;
  }
  .create-post-form .title-input {
    width: auto;
  }
}/*# sourceMappingURL=CreatePost.css.map */